<template>
    <home-container :title="step === 1 ? 'Start Investing Here':null" default-href="/home" :with-header="step === 3 ? false : true"> 
        <div v-if="step === 1">
            <div class="row">
                <div class="col-8 mx-auto text-center">
                    <ion-text>Explore different investment<br> plans here</ion-text>
                </div>
            </div>
            <ion-label position='stacked'>Digital Asset</ion-label>
            <ion-item  class="border ion-margin-bottom" lines="none">
                <ion-select v-model='asset'  placeholder="Select Asset" :interface-options="{header: 'Select Asset Digital'}">
                    <ion-select-option :disabled = "i !== 'Bitcoin'" v-for="(i,index) in ['Bitcoin','Etherum','Dodge','Atom','Bitcoin Cash']"  :key='index'>{{i}}</ion-select-option>
                </ion-select>
            </ion-item>
            <ion-label position='stacked'>Investment Plan</ion-label>
            <ion-item  class="border ion-margin-bottom" lines="none">
                <ion-select v-model='plan' interface='action-sheet'  placeholder="Select Plan" :interface-options="{header: 'Select Investment plans'}">
                    <ion-select-option v-for="(i,index) in ['Invest daily','Invest twice a week','Invest every week','Invest twice a month','Invest every month']"  :key='index'>{{i}}</ion-select-option>
                </ion-select>
            </ion-item>
            
            <ion-label position='stacked'>Amount</ion-label>
            <ion-item class='border ' lines='none'>
                <ion-label style='padding-right:10px;border-right:1px solid #e0e5eb'>NGN</ion-label>
                <ion-input type="number" @ionChange="convertNgnToBtc" v-model.lazy="in_ngn"></ion-input>
            </ion-item>
            <div class='d-flex justify-content-end ion-margin-bottom'>
                <small :class='in_ngn >= 2000?"text-success":"text-danger"'>Minimum of N2,000</small>
            </div>
            <div v-if='show_btn_value_field'>
                <ion-label position='stacked'>BTC Value</ion-label>
                <ion-item class='border ion-margin-bottom' lines='none'>
                    <ion-label style='padding-right:10px;border-right:1px solid #e0e5eb'>BTC</ion-label>
                    <ion-input readonly :value='in_btc'></ion-input>
                </ion-item>
            </div>
            <ion-button strong :disabled="!authorized" @click.prevent="step++"  class='ion-margin-horizontal my-3' expand="block">
                <ion-label>Authorize</ion-label>
            </ion-button>
            <div class="row">
                <div class="col-11 mx-auto text-center">
                    <ion-text >
                        <small class='small'>Kindly note information provided in this section is encrypted and cannot be accessed by anyone</small>
                    </ion-text>
                </div>
            </div>
        </div>
        
        <div v-if="step === 2">
            <ion-item lines="none" class="ion-no-padding">
                <ion-button color='dark' slot="start" fill="clear" @click.prevent="step--">
                <ion-icon slot="icon-only" :icon="arrowBackOutline"></ion-icon>
                </ion-button>
                <ion-text slot="start"><h1 class="">Confirmation</h1></ion-text>
            </ion-item>
            <div class="col-10 mx-auto text-center ion-margin-bottom">
                <ion-text>A journey of a thousand miles begins with a <br>single step</ion-text>
            </div>
            <ion-list>
                <ion-item lines="full">
                <ion-label slot="start"><small>What you get</small></ion-label>
                <ion-label slot="end" class='ion-text-wrap'>{{in_btc}}</ion-label>
                </ion-item>
                <ion-item lines="full">
                <ion-label slot="start"><small>Plan</small></ion-label>
                <ion-label slot="end" class='ion-text-wrap'>{{plan}}</ion-label>
                </ion-item>
                <ion-item lines="full">
                <ion-label slot="start"><small>Set Amount</small></ion-label>
                <ion-label slot="end">N{{in_ngn}}</ion-label>
                </ion-item>
                <ion-item lines="full">
                <ion-label slot="start"><small>Charges</small></ion-label>
                <ion-label slot="end">$0.00</ion-label>
                </ion-item>
            </ion-list>
            <ion-button strong class="ion-margin-horizontal my-4" expand="block" @click.prevent="process">
                <ion-label>Activate Plan</ion-label>
            </ion-button>
            <div class="row">
                <div class="col-11 mx-auto text-center">
                    <ion-text >
                        <small class='small'>Kindly note information provided in this section is encrypted and cannot be accessed by anyone</small>
                    </ion-text>
                </div>
            </div>
        </div>
        <div v-if="step === 3" style="height: 100vh" class="d-flex flex-column align-items-center justify-content-center" >
            <ion-icon class="icon-container mb-5" style="font-size: 4rem" :icon="checkmarkOutline"></ion-icon>
            <ion-text><h1 class="">Congratulations</h1></ion-text>
            <p class='lead text-center'>Your Investment plan has been <br>successfully implemented</p>
            <ion-button strong @click.prevent="router.push('/home/dashboard')" class="ion-margin-horizontal my-4 w-100" expand="block">
                <ion-label>Proceed to portfolio</ion-label>
            </ion-button>
        </div>
        
    </home-container>
</template>
<script>
import { defineComponent } from "vue";
import { checkmarkOutline,arrowBackOutline} from "ionicons/icons";
import { useRouter } from "vue-router";
import { loadingController } from "@ionic/vue";
    export default defineComponent({
        data() {
            return {
                btc_to_naira:19274484.81,
                btc_to_dollar:47186.2,
                show_btn_value_field:false,
                asset:null,
                plan:null,
                step: 1,
                in_btc: null,
                in_ngn: null,
            }
        },
        watch: {
            in_ngn(value) {
                if(!value || value.length < 1){
                    this.show_btn_value_field = false;
                }
             },
        },
        setup() {
            const router = useRouter();
            return {
                router,checkmarkOutline,arrowBackOutline
            };
        },
        computed:{
            authorized(){
                return this.asset && this.plan && this.in_btc
            }
        },
        
        async ionViewWillLeave(){
            setTimeout(() => this.resetFields()) ;
        },
        methods: {
            async process(){
                let loading = await loadingController.create({
                    message: "Please wait...",
                });
                loading.present();
                setTimeout(() => {
                    this.step++;
                    loading.dismiss();
                }, 1000);
            },
            resetFields() {
                this.show_btn_value_field = false,
                this.asset = null;
                this.plan = null;
                this.step = 1;
                this.in_btc =  null;
                this.in_ngn =  null;
            },
            convertNgnToBtc(){
                
                if(this.in_ngn && Number(this.in_ngn) >= 2000){
                    this.in_btc = (Number(this.in_ngn) / this.btc_to_naira).toPrecision(10)
                    this.show_btn_value_field = true;
                }
            }
        }
});

</script>
